/* Projects Page Styles */
.projects-page {
  background: #f4f6f8;
  color: var(--navy-heading);
  font-family: 'Inter', 'Segoe UI', 'Roboto', Arial, sans-serif;
}

/* Hero Section */
.projects-hero {
  background: linear-gradient(160deg, #f4f6f8 0%, #e3f0fa 100%);
  padding: 6rem 0 5rem 0;
  text-align: center;
  border-bottom: 1px solid var(--border-light);
}
.projects-hero-title {
  font-size: 3rem;
  font-weight: 650;
  color: var(--navy-heading);
  margin-bottom: 1.2rem;
  letter-spacing: 1px;
}
.projects-hero-subtitle {
  font-size: 1.3rem;
  color: var(--text-secondary);
  font-weight: 500;
  max-width: 700px;
  margin: 0 auto;
  line-height: 1.5;
}

/* Projects List */
.projects-list-section {
  padding: 4rem 0 4rem 0;
}
.projects-list {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2.5rem;
  max-width: 1300px;
  margin: 0 auto;
}
.project-card {
  background: #fff;
  border-radius: 1.2rem;
  box-shadow: 0 4px 24px #57bceb22;
  border: 1.5px solid var(--border-light);
  display: flex;
  flex-direction: row;
  overflow: hidden;
  transition: box-shadow 0.2s, transform 0.2s, background 0.2s, border 0.2s;
  min-height: 260px;
  max-width: 1200px;
  margin: 0 auto;
}
.project-card:hover {
  box-shadow: 0 0 7px 0.1px var(--curious-blue-transparent);
  border: 1.5px solid var(--curious-blue-transparent);
  transform: translateY(-2px) scale(1.01);
}
.project-photo-section {
  width: 50%;
  background: none;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.5rem;
  box-sizing: border-box;
}
.project-photo {
  width: 100%;
  height: auto;
  aspect-ratio: 3/2;
  object-fit: cover;
  display: block;
  border-radius: 1rem;
  box-shadow: 0 2px 8px #e5e7eb33;
}
.project-info-section {
  width: 50%;
  padding: 2rem 2rem 1.5rem 2rem;
  display: flex;
  flex-direction: column;
  gap: 0.7rem;
  justify-content: center;
}
.project-title-date {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
}
.project-title-status-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1.2rem;
}
.project-title {
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--science-blue);
  margin-bottom: 0.2rem;
  flex: 1 1 auto;
}
.project-date {
  font-size: 1rem;
  color: var(--text-secondary);
  font-weight: 500;
}
.project-status {
  font-size: 1rem;
  font-weight: 700;
  padding: 0.3rem 1.1rem;
  border-radius: 1rem;
  display: inline-block;
  margin-bottom: 0;
  margin-top: 0;
  letter-spacing: 0.5px;
  width: fit-content;
  white-space: nowrap;
}
.project-status-completed {
  background: #e0f7fa;
  color: #0097a7;
}
.project-status-ongoing {
  background: #fffbe7;
  color: #f59e42;
}
.project-status-planned {
  background: #e3e8f0;
  color: #64748b;
}
.project-explanation {
  color: var(--text-secondary);
  font-size: 1.08rem;
  line-height: 1.6;
  margin-top: 0.5rem;
}

@media (max-width: 1300px) {
  .project-card {
    max-width: 98vw;
  }
}

@media (max-width: 900px) {
  .projects-list {
    grid-template-columns: 1fr;
    gap: 1.2rem;
  }
  .project-card {
    flex-direction: column;
    width: 90vw;
    max-width: 350px;
    margin: 0 auto;
    min-height: unset;
  }
  .project-photo-section {
    width: 100%;
    padding: 1rem;
  }
  .project-photo {
    border-radius: 1rem;
  }
  .project-info-section {
    padding: 1.2rem 1rem 1rem 1rem;
  }
}

.project-carousel {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 1rem;
  overflow: hidden;
}
.carousel-arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(255,255,255,0.85);
  border: none;
  border-radius: 50%;
  width: 2rem;
  height: 2rem;
  font-size: 1.1rem;
  color: var(--science-blue);
  cursor: pointer;
  z-index: 2;
  box-shadow: 0 2px 8px #e5e7eb33;
  transition: background 0.2s, color 0.2s;
}
.carousel-arrow.left {
  left: 0.5rem;
}
.carousel-arrow.right {
  right: 0.5rem;
}
.carousel-arrow:hover {
  background: var(--science-blue);
  color: #fff;
} 